@charset "UTF-8";
html {
  box-sizing: initial;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  width: 100vw;
  height: 100%;
  position: relative;
  margin: 0;
  padding: 0;
  font-family: "Open Sans", sans-serif;
  background-color: #F3F6F9;
  overflow-x: hidden;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

.container {
  display: flex;
  width: 100%;
  height: 100%;
}
.container._row {
  flex-direction: row;
}
.container._column {
  flex-direction: column;
}
.container._space-between {
  justify-content: space-between;
}

.hidden {
  display: none;
}

.show {
  display: block;
  z-index: 3;
}

.overlay {
  position: absolute;
  top: 80px;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 2;
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5));
}

nav {
  width: 100%;
  height: 80px;
  background: #FFFFFF 0% 0% no-repeat padding-box;
  box-shadow: 0px 3px 6px #00000029;
  z-index: 3;
}
nav .nav-left a {
  display: block;
  width: 100%;
  height: 100%;
}
nav .nav-left a img {
  height: 100%;
}
@media screen and (min-width: 1024px) {
  nav .nav-left {
    margin-left: 10%;
  }
}
nav .nav-right {
  margin-right: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media screen and (min-width: 1024px) {
  nav .nav-right {
    margin-right: 10%;
  }
}

.burger-button {
  width: 3rem;
  height: 3rem;
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.burger-button .burger-span {
  width: 2rem;
  height: 3px;
  display: block;
  margin: 0;
  padding: 0;
  background-color: #38485C;
  border: none;
  border-radius: 3px;
  transition: all 250ms ease-in-out;
}
.burger-button .burger-span:not(:last-child) {
  margin-bottom: 5px;
}

.menu-burger {
  width: 80%;
  max-width: 340px;
  height: 162px;
  padding: 0.5rem;
  position: absolute;
  top: 45px;
  right: 2rem;
  background: #FFFFFF 0% 0% no-repeat padding-box;
  box-shadow: 0px 0px 12px #38485C29;
  border-radius: 8px;
  transition: all 250ms ease-in-out;
}
@media screen and (min-width: 1024px) {
  .menu-burger {
    right: 10%;
  }
}
.menu-burger .menu-button {
  width: 100%;
  margin: auto;
  height: 31.6666666667%;
  border-radius: 8px;
  transition: background-color 150ms ease-in-out;
}
.menu-burger .menu-button:hover, .menu-burger .menu-button._active {
  background-color: #F3F6F9;
}
.menu-burger .menu-button:not(:last-child) {
  margin-bottom: 5px;
}
.menu-burger .menu-button a {
  width: 100%;
  height: 100%;
  padding-left: 0.5rem;
  display: flex;
  flex-flow: row wrap;
  justify-content: left;
  align-items: center;
  color: #38485C;
  font-family: "Open Sans", sans-serif;
}

.btn {
  display: block;
  width: 120px;
  height: 38px;
  font-size: clamp(14px, 2vw, 16px);
  font-family: "Nunito", sans-serif;
  font-weight: 700;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}
@media screen and (min-width: 768px) {
  .btn {
    width: 136px;
    height: 48px;
  }
}
.btn.btn-gray {
  background-color: #5C6A7A;
  color: #FFFFFF;
  transition: all 150ms ease-in-out;
}
.btn.btn-gray:hover {
  background-color: #505E70;
}
.btn.btn-gray:focus {
  width: 100px;
  border: 4px solid #38485C;
}
.btn.btn-yellow {
  background-color: #FFDB2E;
  color: #38485C;
  transition: all 150ms ease-in-out;
}
.btn.btn-yellow:hover {
  background-color: #FDD306;
}
.btn.btn-yellow:focus {
  width: 100px;
  border: 4px solid #D9B608;
}
.btn-wrapper {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  align-items: center;
}
@media screen and (min-width: 1024px) {
  .btn-wrapper {
    max-width: 350px;
    margin-top: 2rem;
    margin-left: auto;
  }
}

.btn-disabled, .btn.btn-gray:disabled, .btn.btn-yellow:disabled {
  width: 100px;
  background-color: #E6EBF0;
  color: #9EAAB8;
}

.register-form {
  width: 80%;
  max-width: 1085px;
  margin: 3.5rem auto;
  padding: 1rem;
  background: #FFFFFF 0% 0% no-repeat padding-box;
  border: 1px solid #DFE7F0;
  border-radius: 22px;
}
@media screen and (min-width: 768px) {
  .register-form {
    padding: 3rem;
    margin: 105px auto 148px auto;
  }
}
.register-form h3 {
  margin: 1.5rem 0 0 0;
  font-size: clamp(18px, 2vw, 24px);
  color: #38485C;
  font-family: "Nunito", sans-serif;
}
@media screen and (min-width: 768px) {
  .register-form h3:last-of-type {
    margin-top: 4rem;
  }
}
.register-form p {
  font-size: clamp(14px, 2vw, 16px);
  color: #505E70;
}
.register-form hr {
  height: 1px;
  border: none;
  background-color: #DFE7F0;
}
.register-form .form-wrapper {
  margin: 1rem auto;
  display: flex;
  flex-direction: column;
}
@media screen and (min-width: 768px) {
  .register-form .form-wrapper.inline {
    flex-flow: row;
    gap: 1rem;
  }
}
@media screen and (min-width: 768px) {
  .register-form .form-wrapper.small {
    width: 50%;
    margin: 0;
  }
}
.register-form .form-wrapper .form-group {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 5px;
  margin: 0.5rem 0;
}
.register-form .form-wrapper .form-group.conditions {
  flex-direction: row;
  align-items: center;
  transition: all ease-in-out 150ms;
}
@media screen and (min-width: 768px) {
  .register-form .form-wrapper .form-group.conditions {
    margin-top: 1.5rem;
  }
}
.register-form .form-wrapper .form-group.conditions:hover {
  background-color: #F3F6F9;
  border-radius: 1.5rem;
}
.register-form .form-wrapper .form-group label {
  font-size: 13px;
  font-family: "Nunito", sans-serif;
  font-weight: 500;
  color: #38485C;
}
.register-form .form-wrapper .form-group label.accept-politics {
  font-size: clamp(14px, 2vw, 16px);
}
.register-form .form-wrapper .form-group input, .register-form .form-wrapper .form-group select {
  padding: 0.5rem;
  background-color: #F3F6F9;
  border: 1px solid #8A98A8;
  border-radius: 4px;
}
.register-form .form-wrapper .form-group select {
  font-family: "Nunito", sans-serif;
  color: #8A98A8;
}
@media screen and (min-width: 768px) {
  .register-form .form-wrapper .form-group select {
    max-width: 240px;
  }
}
.register-form .form-wrapper .form-group select:focus, .register-form .form-wrapper .form-group select:focus-visible {
  border: 2px solid #005387;
  outline: none;
}
.register-form .form-wrapper .form-group select:active {
  border: 1px solid #005387;
}
.register-form .form-wrapper .form-group input {
  height: 38px;
  color: #38485C;
}
@media screen and (min-width: 1440px) {
  .register-form .form-wrapper .form-group input {
    width: 340px;
  }
}
.register-form .form-wrapper .form-group input:focus, .register-form .form-wrapper .form-group input:focus-visible {
  border: 2px solid #005387;
  outline: none;
}
.register-form .form-wrapper .form-group input:active {
  border: 1px solid #005387;
}
.register-form .form-wrapper .form-group input::placeholder {
  color: #8A98A8;
}
.register-form .form-wrapper .form-group input.error {
  border: 2px solid #C93B3B !important;
}
.register-form .form-wrapper .form-group input.valid {
  border: 2px solid #26874A !important;
}
.register-form .form-wrapper .form-group input.disabled {
  background-color: #F3F6F9;
  border: 1px solid #D1D8E0 !important;
  color: #D1D8E0;
}
.register-form .form-wrapper .form-group input.agendaInput {
  color: #8A98A8;
  font-family: "Nunito", sans-serif;
}
@media screen and (min-width: 768px) {
  .register-form .form-wrapper .form-group input.agendaInput {
    width: 80%;
    max-width: 250px;
  }
}
@media screen and (min-width: 768px) {
  .register-form .form-wrapper .form-group input.input-number {
    max-width: 120px;
  }
}
.register-form .form-wrapper .form-group input.checkbox {
  width: 20px;
  height: 20px;
}
.register-form .form-wrapper .form-group input.checkbox:after {
  content: " ";
  width: 20px;
  height: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #FFFFFF;
  color: #38485C;
  font-weight: 700;
  border: 2px solid #38485C;
  border-radius: 4px;
}
.register-form .form-wrapper .form-group input.checkbox:checked:after {
  content: "âœ”";
}
.register-form .form-wrapper .form-group .error-message {
  width: 100%;
  font-size: clamp(12px, 1.2vw, 14px);
  font-weight: 700;
  color: #C93B3B;
}

footer {
  background-color: #333333;
  border-top: 2px solid #FFDB2E;
  opacity: 1;
  overflow: hidden;
}
@media screen and (min-width: 1024px) {
  footer {
    height: 254px;
  }
}
footer .footer-div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
@media screen and (min-width: 768px) {
  footer .footer-div {
    padding: 0 10%;
    flex-direction: row;
    justify-content: space-between;
  }
}
footer .footer-div.top .icons-wrapper {
  width: 285px;
  margin: 1rem 0;
  padding: 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
footer .footer-div.top .icons-wrapper i:hover::before {
  color: #DFE7F0;
}
footer .footer-div.top .icons-wrapper i::before {
  color: #FFFFFF;
}
footer .footer-div.bottom {
  height: 100%;
}
footer .footer-div.bottom .links-wrapper {
  width: 80%;
  margin: 1rem auto;
  padding: 0;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
@media screen and (min-width: 1024px) {
  footer .footer-div.bottom .links-wrapper {
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
  }
}
footer .footer-div.bottom .links-wrapper li {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
footer .footer-div.bottom .links-wrapper li a {
  color: #FFFFFF;
  font-size: clamp(14px, 2vw, 16px);
  font-family: "Open Sans", sans-serif;
  text-decoration: underline;
}
footer .footer-div.bottom .links-wrapper li a:hover {
  color: #DFE7F0;
}
footer .footer-div .footer-logo {
  width: 260px;
}
footer hr {
  width: 80%;
  margin: 1rem auto;
}